header {
    background-color: darkslategrey;
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

header h1 {
    margin: 0;
    padding: 12px;
    font-size: 26px;
}

header button {
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 16px;
    padding-right: 16px;
    font-family: 'Dosis', sans-serif;
    cursor: pointer;
}

header button:focus {
    outline: none;
}

.cardboard {
    padding: 24px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.card {
    width: 200px;
    height: 200px;
    background-color: darkgray;
    border: 4px solid grey;
    border-radius: 12px;
    margin: 12px;
    -webkit-filter: brightness(0);
            filter: brightness(0);
    -webkit-transition: 1s -webkit-filter ease-out;
    transition: 1s -webkit-filter ease-out;
    -o-transition: 1s filter ease-out;
    transition: 1s filter ease-out;
    transition: 1s filter ease-out, 1s -webkit-filter ease-out;
}

.card.visible {
    -webkit-filter: brightness(1);
            filter: brightness(1);
}


